.button {
  font: inherit;
  display: block;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  color: white;
  background: radial-gradient(
    80.75% 950.72% at 30% 323.08%,
    #13b582 0%,
    #066877 100%
  );
  cursor: pointer;
  outline: none;
  text-decoration: none;
  text-align: center;
  border: none;
  -webkit-backdrop-filter: blur(12px);

  backdrop-filter: blur(12px);
  /* Note: backdrop-filter has minimal browser support */

  border-radius: 7px;
  font-size: 1.2rem;
}
.button--transparent {
  background: transparent;
  -webkit-backdrop-filter: blur(0);

  backdrop-filter: blur(0);
}

@media (max-width: 342px) {
  .button {
    font-size: 1.1rem;
  }
}
