.navbar {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 100;
  height: 10vh;
  padding: 0;
  margin: 0;
  background: white;
  box-shadow: 2px -3px 5px rgba(0, 0, 0, 0.1);
  list-style: none;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar li {
  padding-bottom: 0.5rem;
}

.navbar li:nth-of-type(2) {
  margin: 0 2rem;
}

.navbar li a {
  text-decoration: none;
  color: #dcdcdc;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navbar a p {
  font-size: 0.7rem;
  margin: 0.2rem 0 0 0;
  position: absolute;
  bottom: 0;
}

.navbar li a.active svg path {
  fill: #0fd79c;
}
.navbar li a.active p {
  color: #0fd79c;
}
.navbar li:nth-of-type(2) a.active svg path {
  stroke: #0fd79c;
  fill: white;
}

@media (min-height: 490px) {
  .navbar a p {
    bottom: 0.3rem;
  }
}
@media (min-height: 590px) {
  .navbar a p {
    bottom: 0.5rem;
  }
}
@media (min-height: 610px) {
  .navbar a p {
    bottom: 0.5rem;
  }
}
@media (min-height: 640px) {
  .navbar a p {
    bottom: 0.7rem;
  }
}
@media (min-height: 685px) {
  .navbar li {
    padding-bottom: 1rem;
  }
  .navbar a p {
    bottom: 1rem;
  }
}
@media (min-height: 740px) {
  .navbar a p {
    bottom: 1.2rem;
  }
}
