.cour-item {
  padding: 0.5rem 1rem;
  border-bottom: 1px solid rgba(160, 160, 160, 0.19);
}

.cour-item__info {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.cour-item__info h3 {
  margin: 0;
  font-weight: normal;
  font-size: 0.9rem;
  line-height: 1.1rem;
  /* identical to box height */
  color: rgba(0, 0, 0, 0.22);
}

.cour-item__info a {
  text-decoration: none;
  color: #0fd79c;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.cour-item__info a h2 {
  font-size: 0.8rem;
  font-weight: 500;
  margin: 0 5px 0 0;
  line-height: 1rem;
}

.cour-item__thumbnail {
  width: 100%;
  display: block;
  text-decoration: none;
  color: white;
  height: 20vh;
  border-radius: 14px;
  padding: 1rem;
  margin: 0.5rem 0 1.5rem 0;
  position: relative;
}

.cour-item__thumbnail h1 {
  font-size: 1.1rem;
  text-shadow: 0px 2px 8px rgba(0, 0, 0, 0.4);
  line-height: 1.2rem;
  margin: 0;
}

.normal {
  font-weight: normal;
}

.cour-item__thumbnail-play {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%);
}

.cour-item__thumbnail-play {
  width: 25px;
  height: 28px;
}

.cour-item__thumbnail-lessons {
  position: absolute;
  right: 0;
  bottom: 0;
  margin: 1rem;
  background: #f2c94c;
  border: 1px solid white;
  color: white;
  font-size: 0.2rem;
  padding: 1px 4px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.2);
}

@media (min-width: 400px) {
  .cour-item__thumbnail h1 {
    font-size: 1.3rem;
  }
  .cour-item__thumbnail-play {
    top: calc(10vh - 2rem + 30px);
  }
  .cour-item__thumbnail-play {
    width: 31px;
    height: 36px;
  }
}
@media (min-height: 550px) {
  .cour-item__thumbnail-play {
    top: calc(10vh - 2rem + 30px);
  }
}
