.drawer-profile {
  position: fixed;
  top: 0;
  left: 0;
  cursor: pointer;
  width: 80%;
  height: 90vh;
  padding: 1rem;
  z-index: 50;
  background: radial-gradient(
    422.36% 99.86% at 93.18% 90.95%,
    rgba(19, 181, 130, 0.82) 0%,
    rgba(6, 104, 119, 0.82) 100%
  );
  color: white;
  backdrop-filter: blur(12px);
}

.drawer-profile__profile {
  display: flex;
  margin: 1rem 0;

  align-items: center;
  width: 100%;
  height: 70px;
}

.drawer-profile__info {
  height: 100%;
  display: flex;
  flex-flow: column;
  justify-content: space-around;
}

.drawer-profile__info h2,
.drawer-profile__info h3,
.drawer-profile__info p {
  margin: 0;
}

.drawer-profile__info h2 {
  font-size: 1rem;
}

.drawer-profile__info h3 {
  font-size: 0.8rem;
}
.drawer-profile__info p {
  font-size: 0.7rem;
  font-weight: 700;
}
.drawer-profile__avatar {
  margin-right: 1rem;
  width: 70px;
  height: 100%;
  border-radius: 50%;
  border: 2px solid white;
  background: url("./img/avatar-pupil.jpg") no-repeat center center;
  background-size: cover;
}

.drawer-profile__ad {
  position: absolute;
  width: calc(100% - 2rem);
  height: 100px;
  bottom: 1rem !important;
  left: 1rem;
  color: #ccc;
  border-radius: 6px;
  background: url("img/ad.jpg") no-repeat center center / cover;
  background-size: cover;
}

@media (min-width: 340px) {
  .drawer-profile {
    padding: 1.5rem;
  }
}

@media (min-width: 360px) {
  .drawer-profile {
    width: 75%;
  }
}

@media (min-width: 400px) {
  .drawer-profile {
    width: 70%;
  }
}

/* Height */
@media (min-height: 540px) {
  .drawer-profile__profile {
    margin: 2rem 0 3rem 0;
  }
  .drawer-profile__actions {
    margin-top: 1rem !important;
  }
}
@media (min-height: 650px) {
  .drawer-profile__actions {
    margin-top: 2rem !important;
  }
}
@media (min-height: 650px) {
  .drawer-profile__ad {
    height: 120px;
  }
}
@media (min-height: 680px) {
  .drawer-profile__ad {
    height: 140px;
  }
}
@media (min-height: 715px) {
  .drawer-profile__actions {
    margin-top: 3rem !important;
  }
}
@media (min-height: 740px) {
  .drawer-profile__ad {
    height: 160px;
  }
}
@media (min-height: 760px) {
  .drawer-profile__profile {
    margin: 3rem 0 3rem 0;
  }
  .drawer-profile__ad {
    height: 180px;
  }
}
@media (min-height: 810px) {
  .drawer-profile__profile {
    margin: 4rem 0 3rem 0;
  }
}
@media (min-height: 810px) {
  .drawer-profile__profile {
    margin: 4rem 0 3rem 0;
  }
}
