.preview {
  position: fixed;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  align-items: space-between;
  z-index: 100;
}

.preview .preview__swipe {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
}
.preview__actions {
  align-self: center;
  position: absolute;
  bottom: 15%;
  width: 100%;
  padding: 0 2rem;
  z-index: 100;
  margin: 0;
}

.swiper-pagination-custom-bullet {
  height: 12px;
  width: 12px;
  margin-right: 0.5rem !important;
  background: rgba(255, 255, 255, 0.33);
}
.swiper-pagination-bullet-active {
  background: white;
}

.preview__actions button,
.preview__actions a {
  width: 100%;
  margin-top: 1rem;
}
