.profile {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  height: 24vh;

  background: radial-gradient(
      22.36% 99.86% at 93.18% 1.95%,
      rgba(19, 181, 130, 0.82) 0%,
      rgba(6, 104, 119, 0.82) 100%
    ),
    url("./img/bg-prof.jpg") no-repeat center center;
  backdrop-filter: blur(12px);
  background-size: cover;
}

.profile__cancel {
  right: 2rem;
  cursor: pointer;
  top: 35%;
  z-index: 50;
  width: 20px;
  height: 20px;
  background: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  border: none;
  outline: none;
  position: absolute;
}

.profile__cancel span:first-child {
  background: white;
  width: 100%;
  transform: rotate(45deg);
  position: absolute;
  height: 2px;
  z-index: 50;
}

.profile__cancel span:last-child {
  background: white;
  position: absolute;
  z-index: 50;
  width: 100%;
  transform: rotate(135deg);
  height: 2px;
}

.profile .profile__content {
  width: 100vw;
  height: 100%;
  padding: 0 1.5rem 0.7rem;
  padding-top: 0;
  overflow: hidden;
  position: relative;
}

.profile .profile__content .profile__menu {
  cursor: pointer;
  width: 24px;
  height: 24px;
  background: transparent;
  position: absolute;
  top: calc(24vh * 0.45 - 0.7rem);
  outline: none;
  border: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0;
}

.profile__menu:focus,
.profile__menu:active {
  background: none;
}

.profile .profile__content .profile__menu span {
  background: white;
  display: block;
  height: 2px;
  margin-bottom: 4px;
  width: 100%;
}

.profile .profile__content .profile__info {
  position: absolute;
  bottom: 1rem;
  color: white;
  padding-left: 2px;
  font: inherit;
}

.profile .profile__info h2 {
  margin-bottom: 4px;
  font-size: 1.1rem;
}

.profile .profile__info h3 {
  margin: 0;
  font-size: 0.8rem;
}

.profile__sub {
  position: absolute;
  text-align: right;
  width: 100%;
  right: 0;
  bottom: -1px;
  border-bottom: 1px solid white;
  padding: 0 0.5rem;
}

.profile__sub div {
  position: absolute;
  right: -1rem;
  bottom: -0.2rem;
  width: 9.5rem;
  border-left: 1rem solid transparent;
  border-right: 1rem solid transparent;
  border-bottom: 1.4rem solid white;
}
.sub-actif {
  color: rgba(45, 156, 219, 1);
  font-size: 0.7rem;
  position: relative;
  z-index: 5;
  right: 2px;
}

.sub-actif p {
  margin: 0;
  display: inline;
}

.i-subActif-check {
  margin-left: 6px;
  position: relative;
  top: 1px;
}
.i-subActif-check path {
}

@media (max-width: 340px) {
  .profile .profile__content {
    padding: 0 1rem 0.7rem;
  }

  .profile .profile__info h2 {
    font-size: 0.9rem;
    margin-bottom: 2px;
  }
  .profile .profile__info h3 {
    font-size: 0.7rem;
  }
}
